/*****************
    Reset/Base
******************/
html {
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: auto;
  min-height: 100vh;
  position: relative;
}

img,
picture {
  display: block;
  max-width: 100%;
}

button,
input,
textarea {
  appearance: none;
  font: inherit;
}

ul,
ol {
  padding-inline-start: 0;
}

a {
  text-decoration: none;
}

[hidden] {
  display: none !important;
}

/*****************
    Variables
******************/
:root {
  --hue: 225;
  --sec-hue: 37;
}

/*****************
    No Mixins
******************/ /******************
    Defaults
*******************/
/* Element Defaults */
input,
textarea {
  background-color: #fff;
  border: 2px solid #e2e2e7;
  outline: none;
  padding: 0.625em 1em;
  transition: border-color 200ms ease;
}
input:hover,
textarea:hover {
  border-color: hsl(var(--hue), 80%, 48%);
}
input:focus,
textarea:focus {
  border-color: hsl(var(--hue), 80%, 48%);
}

/* Containers */
.container {
  display: flex;
  width: 100%;
  max-width: 1300px;
  margin: auto;
}

.container-fluid {
  width: 100%;
}

/* Section Header */
.section-header {
  text-align: center;
  margin-bottom: 2em;
}
.section-header h2 {
  font-size: clamp(1.75rem, 3vw, 2rem);
}
.section-header p {
  color: #555;
}

/* Page Heading */
.page-heading {
  border-radius: 1em;
  margin-top: 1em;
  padding: 3em 0;
}
.page-heading p {
  margin-bottom: 0;
}

/* Helpers */
.mt-0 {
  margin-top: 0;
}

.mt-1 {
  margin-top: 0.5em;
}

/*****************
    Typography
******************/
body {
  font-family: -apple-system, BlinkMacSystemFont, "Inter", system-ui, sans-serif;
}

/* Heading Styles */
h1,
h2,
h3,
h4 {
  font-weight: 500;
  margin-bottom: 0.3em;
  line-height: 1;
}

:is(h1, h2, h3) span {
  color: hsl(var(--hue), 80%, 48%);
}

h1,
h2 {
  font-family: "DM Serif Display", serif;
}

h1 {
  font-size: clamp(2rem, 4vw, 2.75rem);
}

h2 {
  font-size: clamp(1.75rem, 3vw, 2rem);
}

h3 {
  font-size: clamp(1.25rem, 2vw, 1.5rem);
}

/* Link Styles */
a {
  font-size: inherit;
  color: #0066dd;
  text-decoration: underline 2px transparent;
  transition: text-decoration-color 200ms ease-in-out;
}
a:focus, a:focus-visible {
  border-color: hsl(var(--hue), 80%, 48%);
  outline-color: hsl(var(--hue), 80%, 48%);
}
a:hover {
  text-decoration-color: #0066dd;
}

/* Paragraph Styles */
p {
  color: #33334b;
  font-size: 1rem;
  line-height: 1.4;
  margin-bottom: 1em;
}

/*****************
    Buttons
******************/
button {
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  cursor: pointer;
  outline: none;
}
button:focus, button:focus-visible {
  border-color: hsl(var(--hue), 80%, 48%);
  outline-color: hsl(var(--hue), 80%, 48%);
}

.btn, .btn-outline--primary, .btn--primary, .btn-outline--black, .btn--black {
  display: inline-flex;
  align-items: center;
  gap: 0.25em;
  border: 1.5px solid transparent;
  border-radius: 0.5em;
  font-size: 0.875rem;
  font-weight: 600;
  padding: 0.5em 0.625em;
  transition: background-color 180ms ease-out, color 180ms ease-out;
}
@media (min-width: 600px) {
  .btn, .btn-outline--primary, .btn--primary, .btn-outline--black, .btn--black {
    padding: 0.75em 0.875em;
  }
}
.btn:hover, .btn-outline--primary:hover, .btn--primary:hover, .btn-outline--black:hover, .btn--black:hover {
  text-decoration: none;
}
.btn--black {
  background-color: #0a0a0a;
  color: #fff;
}
.btn--black:hover {
  background-color: #555;
}
.btn-outline--black {
  background-color: #fff;
  color: #0a0a0a;
  border-color: #0a0a0a;
}
.btn-outline--black:hover {
  background-color: #0a0a0a;
  color: #fff;
}
.btn--primary {
  background-color: hsl(var(--hue), 80%, 48%);
  color: #fff;
}
.btn--primary:hover {
  background-color: #fff;
  color: hsl(var(--hue), 80%, 48%);
}
.btn-outline--primary {
  background-color: #fff;
  color: hsl(var(--hue), 80%, 48%);
  border-color: hsl(var(--hue), 80%, 48%);
}
.btn-outline--primary:hover {
  background-color: hsl(var(--hue), 80%, 48%);
  color: #fff;
}

/*****************
    Images
******************/
figure {
  margin: 2em 0;
}
figure img {
  border-radius: 0.5em;
  margin-bottom: 0.5em;
}

figcaption {
  color: #666;
  font-size: 0.75rem;
  font-weight: 500;
  text-align: center;
}

/*****************
    Modal
******************/
.modal {
  display: none;
  position: fixed;
}
.modal::before {
  position: fixed;
  content: "";
  inset: 0;
  background-color: #0a0a0a;
  opacity: 0.7;
}
.modal.show {
  display: block;
}
.modal__container {
  background-color: #fff;
  border-radius: 1em;
  border: 2px solid #666;
  min-width: 300px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 1.5em;
}
.modal h3 {
  margin-bottom: 1em;
}
.modal button {
  display: block;
  margin-left: auto;
}

/*****************
    Header Section
******************/
.header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0.5em 0;
  position: relative;
}
.header__logo {
  max-width: 160px;
}
.header__nav_list {
  background-color: #f5f5f7;
  border-radius: 0.5em;
  display: flex;
  align-items: center;
  gap: 1.25em;
  list-style: none;
  padding: 0.625em 1.75em;
}
.header__nav_list li:not(:last-of-type) {
  border-right: 2px solid #ccc;
  padding-right: 1.25em;
}
.header__nav_list li a {
  color: #0a0a0a;
  font-family: "DM Serif Display", serif;
  font-size: 1.125rem;
}
@media (max-width: 500px) {
  .header__nav_list {
    display: none;
  }
}
.header__menu-icon {
  background-color: #f5f5f7;
  border-radius: 0.25em;
  border: 2px solid transparent;
  color: #0a0a0a;
  padding: 0.4em 0.5em;
  display: none;
}
@media (max-width: 500px) {
  .header__menu-icon {
    display: flex;
  }
}
.header__menu-icon:hover {
  background-color: #e2e2e7;
}
.header__menu-icon:focus, .header__menu-icon:focus-visible {
  border-color: #666;
}
.header__mobile_menu {
  background-color: #fff;
  border-radius: 0.5em;
  box-shadow: -2px 1px 5px hsla(var(--hue), 50%, 20%, 0.25);
  list-style: none;
  padding: 0;
  position: absolute;
  right: 2.875em;
  top: 2.125em;
  overflow: hidden;
  transform-origin: top right;
  animation: openMenu 200ms ease-out;
  display: none;
}
@keyframes openMenu {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}
.header__mobile_menu.active {
  display: block;
}
.header__mobile_menu li:last-of-type {
  border-top: 2px solid #e2e2e7;
}
.header__mobile_menu li a {
  display: block;
  color: #33334b;
  padding: 1em 2.5em 1em 1.5em;
}
.header__mobile_menu li a:hover {
  background-color: #f5f5f7;
}

/*****************
    Footer Section
******************/
.footer {
  border-top: 1px solid #e2e2e7;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 1em;
}
.footer__nav {
  display: flex;
  align-items: center;
  column-gap: 20px;
}
.footer__logo {
  max-width: 140px;
}
.footer__links {
  list-style: none;
  display: flex;
  column-gap: 10px;
}
.footer__links li {
  font-size: 0.875rem;
}
.footer__copyright {
  color: #666;
}
@media (max-width: 450px) {
  .footer__copyright {
    width: 100%;
    text-align: center;
  }
}

/*****************
    Form Layout
******************/
.form {
  max-width: 400px;
  margin: 0 auto;
}
.form__heading {
  text-align: center;
  margin-bottom: 1em;
}
.form__control {
  display: flex;
  flex-direction: column;
  gap: 0.125em;
  margin-bottom: 2em;
}
.form label {
  color: #33334b;
  cursor: pointer;
  font-size: 0.875rem;
}
.form input[type=text],
.form input[type=tel],
.form input[type=email],
.form textarea {
  border-radius: 0.5em;
}
.form .input-error {
  color: red;
  font-size: 0.75rem;
  margin-bottom: 0;
  text-align: end;
}
.form__buttons {
  display: flex;
  column-gap: 1em;
  justify-content: end;
}

/*****************
    Sidebar Section
******************/
.sidebar {
  background-color: #fff;
  border-left: 1px solid #e2e2e7;
  border-right: 1px solid #e2e2e7;
  flex: 1 0 300px;
  min-height: 100vh;
  position: relative;
  display: none;
  transform: translateX(100%);
  transition: transform 300ms ease;
}
@media (min-width: 1000px) {
  .sidebar {
    display: none;
    transform: translateX(100%);
  }
  .sidebar.show {
    display: block;
    transform: translateX(0);
  }
}
.sidebar-container {
  display: inline-block;
  position: sticky;
  top: 0;
  padding: 1em;
  width: 100%;
}
.sidebar__header {
  display: grid;
  gap: 1em;
}
.sidebar .btn--black {
  border-radius: 1.5em;
  justify-content: center;
  width: 100%;
}
.sidebar .btn--black:hover {
  background-color: hsl(var(--hue), 80%, 48%);
}
.sidebar input {
  border-radius: 1.5em;
  font-size: 0.875rem;
}
.sidebar__all-skills {
  margin-top: 1em;
}
.sidebar__all-skills h2 {
  color: #0a0a0a;
  font-family: -apple-system, BlinkMacSystemFont, "Inter", system-ui, sans-serif;
  font-size: clamp(1.0625rem, 2vw, 1.25rem);
  letter-spacing: -0.5px;
  margin-top: 2em;
  margin-bottom: 0.75em;
}
.sidebar__skills {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5em;
}
.sidebar__skills span {
  cursor: default;
  background-color: #f5f5f7;
  border: 2px dashed transparent;
  border-radius: 1.5em;
  color: #33334b;
  font-size: 0.875rem;
  padding: 0.25em 0.75em;
}
.sidebar__skills span:hover {
  border-color: hsl(var(--hue), 80%, 48%);
}

/* Home Page Styles */
/*****************
    Main Container
******************/
.main {
  margin: 0 auto;
  padding: 0.25em 1em;
  position: relative;
  width: 100%;
  max-width: 1000px;
}

/*****************
    Hero Section
******************/
.hero {
  display: flex;
  flex-wrap: wrap;
  gap: 1em;
  margin: 2em 0;
}
@media (max-width: 500px) {
  .hero {
    margin-top: 1em;
  }
}
.hero__profile {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1 1 480px;
  gap: 1em;
  background-color: #f5f5f7;
  border-radius: 1em;
  padding: 2em 1em;
}
.hero__profile_image {
  width: 110px;
  height: 110px;
  background-color: #fff;
  border-radius: 50%;
  pointer-events: none;
  user-select: none;
}
@media (min-width: 500px) {
  .hero__profile_image {
    width: 180px;
    height: 180px;
  }
}
.hero__profile_name {
  font-size: clamp(2rem, 4vw, 2.75rem);
}
.hero__profile_title {
  font-size: clamp(1.25rem, 2vw, 1.5rem);
  font-family: -apple-system, BlinkMacSystemFont, "Inter", system-ui, sans-serif;
  font-weight: 500;
  -webkit-text-fill-color: transparent;
  background: linear-gradient(to right, #1a2a6c, #b21f1f, #fda312);
  -webkit-background-clip: text;
  background-clip: text;
  letter-spacing: -1px;
  margin-bottom: 0.5em;
}
.hero__profile_summary {
  margin-bottom: 0;
}
.hero__description {
  background-color: #f5f5f7;
  border-radius: 1em;
  padding: 2.5em 2em;
  flex: 1 1 300px;
}
.hero__description p:last-of-type {
  margin-bottom: 0;
}

/*****************
    About Section
******************/
.about {
  padding: 70px 0 80px;
}
.about__allcards {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 1em;
}
.about__card {
  background-color: #f5f5f7;
  border-radius: 10px;
  padding: 3em 2.25em;
  flex: 1 1 300px;
}
.about__card_icon {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.75rem;
  height: 40px;
  width: 40px;
}
.about__card__heading {
  margin-top: 2em;
}

/*****************
    Newsletter Card
******************/
.newsletter {
  background-color: #0a0a0a;
  border-radius: 1em;
  display: flex;
  flex-wrap: wrap;
  overflow: hidden;
  margin-bottom: 3em;
}
.newsletter__image {
  flex: 1 0 180px;
  height: 180px;
}
.newsletter__image img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
@media (min-width: 600px) {
  .newsletter__image {
    height: 240px;
  }
}
.newsletter__body {
  display: grid;
  flex: 1 1 480px;
  padding: 2em 2em;
}
.newsletter__body h2 {
  color: #fff;
}
.newsletter__body p {
  color: #e2e2e7;
}
.newsletter__cta {
  align-self: end;
}

/* Blog Page Styles */
/*****************
    Blog Section
******************/
.blog {
  margin: 2em 0 4em;
}
.blog__post-list {
  list-style: none;
  padding: 0;
}
.blog__post-item {
  display: flex;
  border-bottom: 2px solid #e2e2e7;
  gap: 0.5em;
  padding: 1.5em 0;
}
@media (min-width: 600px) {
  .blog__post-item {
    padding-left: 2em;
    padding-right: 2em;
  }
}
.blog__post-item__content {
  display: grid;
  flex: 0 1 76%;
  margin-top: 0.5em;
}
.blog__post-item__content h3 {
  font-size: clamp(1.25rem, 2vw, 1.5rem);
  margin-bottom: 0;
  letter-spacing: 0;
  line-height: 1.2;
}
.blog__post-item__content h3 a {
  color: #0a0a0a;
}
.blog__post-item__content h3 a:hover {
  text-decoration-color: transparent;
  color: hsl(var(--hue), 80%, 48%);
}
.blog__post-item__content p {
  display: none;
}
@media (min-width: 600px) {
  .blog__post-item__content p {
    display: block;
  }
}
.blog__post-item__date {
  color: #666;
}
.blog__post-item__meta {
  align-self: baseline;
  margin-top: 0.5em;
}
.blog__post-item__meta span {
  color: #33334b;
  font-size: 0.75rem;
}
.blog__post-item__meta span:first-of-type {
  background-color: #f5f5f7;
  border-radius: 1.5em;
  font-weight: 500;
  letter-spacing: -0.25px;
  margin-right: 0.5em;
  padding: 0.5em 1em;
}
.blog__post-item__image {
  border-radius: 0.5em;
  overflow: hidden;
  width: 120px;
  height: 120px;
}
.blog__post-item__image img {
  object-fit: cover;
  height: 100%;
  width: 100%;
}
@media (min-width: 600px) {
  .blog__post-item__image {
    width: 220px;
    height: 180px;
  }
}
.blog__tags {
  border-bottom: 1px solid #f2f2f4;
  border-radius: 0.5em;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 0.125em;
  background-color: #f5f5f7;
  padding: 1.25em 1.5em 0.875em;
}
.blog__tags span {
  text-transform: uppercase;
  letter-spacing: 2px;
}
.blog__tags-list {
  list-style: none;
  display: flex;
  align-items: center;
  column-gap: 0.5em;
  padding: 0;
  min-height: 54px;
  overflow-x: auto;
  overflow-y: hidden;
}
.blog__tags-list li:first-of-type a {
  display: flex;
  align-items: center;
  gap: 0.125em;
}
.blog__tags-list li:first-of-type a span {
  color: hsl(var(--hue), 80%, 48%);
  font-size: 0.875rem;
}
.blog__tags-list a {
  background-color: #fff;
  border: 2px solid #e2e2e7;
  border-radius: 0.5em;
  color: #33334b;
  font-size: 0.875rem;
  font-weight: 500;
  padding: 0.5em 0.75em;
  overflow: hidden;
  white-space: nowrap;
}
.blog__tags-list a:hover {
  border-color: hsl(var(--hue), 80%, 48%);
  text-decoration-color: transparent;
}

/* Portfolio Page Styles */
/*****************
    Projects Section
******************/
.projects {
  margin-top: 0 4em;
}
.projects__project {
  display: flex;
  flex-direction: column;
  gap: 1em;
  counter-increment: projectIndex;
  margin-bottom: 4em;
  position: relative;
}
.projects__project::before {
  content: counter(projectIndex);
  position: absolute;
  top: 1em;
  right: 1em;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f5f5f7;
  border-radius: 50%;
  border: 2px solid #e2e2e7;
  color: #33334b;
  font-size: clamp(1.0625rem, 2vw, 1.25rem);
  height: 35px;
  width: 35px;
  z-index: 10;
}
.projects__project:first-of-type::before {
  background-color: hsl(var(--hue), 12%, 10%);
  border-color: hsl(var(--hue), 12%, 30%);
  color: #ccc;
}
.projects__card {
  padding-top: 3em;
  border-radius: 10px;
  position: relative;
  box-shadow: 5px 5px 10px rgba(255, 255, 255, 0.05);
  background-color: hsl(var(--hue), 12%, 10%);
  overflow: hidden;
  transition: border-color 200ms ease-in-out;
}
.projects__card__content {
  padding: 0 2em 1em;
}
.projects__card__heading {
  color: #fff;
  margin-bottom: 0.5em;
}
.projects__card__heading em {
  color: #ccc;
}
.projects__card__badges {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5em;
  margin-bottom: 0.75em;
}
.projects__card__badges span {
  background-color: hsl(var(--hue), 12%, 20%);
  border-radius: 0.5em;
  color: #ccc;
  font-size: 0.75rem;
  padding: 6px 10px;
}
.projects__card__badges span:first-of-type {
  display: flex;
  align-items: center;
  gap: 6px;
}
.projects__card__badges span:first-of-type::before {
  content: "";
  display: block;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background-color: green;
}
.projects__card__desc {
  margin-bottom: 1em;
  color: #e2e2e7;
}
.projects__card__skills {
  border-top: 1.5px solid #555;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 0.375em;
  padding: 1em 2em;
}
.projects__card__skills p {
  color: #e2e2e7;
  margin-bottom: 0;
}
.projects__card__skills span {
  cursor: default;
  background-color: hsl(var(--hue), 12%, 20%);
  border: 2px dashed transparent;
  border-radius: 1.5em;
  color: #ccc;
  font-size: 0.875rem;
  padding: 0.25em 0.75em;
}
.projects__card__skills span:hover {
  border-color: hsl(var(--hue), 80%, 48%);
}
.projects__card:first-child:after {
  background: linear-gradient(darkred, hsl(var(--sec-hue), 98%, 53%));
  content: "";
  clip-path: polygon(0% 0%, 100% 0, 100% 100%, 50% 75%, 0% 100%);
  color: hsl(var(--sec-hue), 98%, 53%);
  position: absolute;
  top: 0;
  left: 2em;
  width: 32px;
  height: 40px;
}
.projects :nth-of-type(2) .projects__card {
  background-color: #f5f5f7;
}
.projects :nth-of-type(2) .projects__card__heading {
  color: #666;
  margin-bottom: 0.5em;
}
.projects :nth-of-type(2) .projects__card__heading em {
  color: #0a0a0a;
}
.projects :nth-of-type(2) .projects__card__badges span {
  background-color: #e2e2e7;
  color: #33334b;
}
.projects :nth-of-type(2) .projects__card__desc {
  color: #33334b;
}
.projects :nth-of-type(2) .projects__card__skills {
  border-top: 2px solid #e2e2e7;
}
.projects :nth-of-type(2) .projects__card__skills p {
  margin-bottom: 0;
  color: #33334b;
}
.projects :nth-of-type(2) .projects__card__skills span {
  background-color: #e2e2e7;
  border-radius: 1.5em;
  color: #33334b;
}
.projects :nth-of-type(2) .projects__card:first-of-type:first-child:after {
  background: linear-gradient(darkblue, #0066dd);
}

/* Contact Page Styles */
/*****************
    Contact Form
******************/
.contact__form {
  background-color: #f5f5f7;
  border-radius: 1em;
  margin: 2em auto;
  max-width: 500px;
  padding: 3em 2em;
}
.contact__form .btn--black {
  padding: 0.75em 2em;
}
.contact__form .btn--black:hover {
  background-color: hsl(var(--hue), 80%, 48%);
}

/*****************
    Social links
******************/
.social {
  margin-top: 4em;
  text-align: center;
}
.social__links {
  background-color: #f5f5f7;
  border-radius: 0.5em;
  list-style: none;
  width: fit-content;
  margin: 0 auto;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  overflow: hidden;
}
.social__links li {
  display: flex;
  align-items: center;
  gap: 0.5em;
  background-color: #0a0a0a;
  cursor: pointer;
}
.social__links li:hover {
  background-color: hsl(var(--hue), 80%, 48%);
}
.social__links a {
  color: #fff;
  display: block;
  height: 100%;
  padding: 1em;
  text-decoration-color: transparent;
  width: 100%;
}